<template>
<div class="Top2" style="min-height:100vh">
  <div class="sp_line_s" />
  
  <div style="text-align:left">
    <button type="button" class="btnBase" v-on:click="pushBtn('BACK')">戻る</button>
  </div>

  <div class="sp_line_s" />
  <ListRow kbn="SOUDAN" isLatest="false" :data="soudanData" :rowIndex="-1"></ListRow>
  <div class="sp_line_m" />

  <button type="button" class="btnBase writeBtn" @click="this.$root.autoScroll('EDIT')">書き込む</button>
  
  <div class="sp_line_m" />

  <div class="dotBar" />

  <div class="sp_line_s" />

  <ListFrame kbn="RESPONSE" :list="this.postList" @rowAction="rowAction"></ListFrame>

  <div class="sp_line_s" />
  
  <div class="dotBar" />

  <subPagenation
    :showPages="showPages"
    :currentPage="currentPage"
    :totalCount="totalCount"
    :perPage="perPage"
    :totalPages="totalPages"
    @currentPage="getCurrentPage"
  ></subPagenation>
  
  <EditFrame ref="edit" kbn="RESPONSE" :params="{soudanId : this.$route.params.soudanId}" @editAction="editAction"></EditFrame>
</div>
</template>

<script>
import axios from 'axios'
import SubPagenation from './SubPagenation.vue';
import EditFrame from './EditFrame.vue';
import ListFrame from './ListFrame.vue';
import ListRow from './ListRow.vue';

export default {
  name: 'SoudanResponse',
  components: {
    SubPagenation,
    EditFrame,
    ListFrame,
    ListRow,
  },
  data() {
    return {
      soudanId : "",

      //ページネーションの設定
      currentPage: 1, //現在のページ（初期は1）
      showPages: 5, //ページネーションを何ページ表示するか（奇数でないとずれる）
      perPage: 20, //1ページの表示件数
      totalCount: 0, //取得したアイテムの総件数
      totalPages: 0, //総ページ数

      nickName : "",
      message : "",
      deleteSecret : "",

      errMsg : "",
      postList : [],
      soudanData : [],

      targetReportId : "",
      soudanPage : 0,
    }
  },
  created : function () {
  },
  computed : {
  },
  mounted () {
  },
  methods : {
    handleKeydown(e) {
      // this.chValue('OUTLINE')
      if (this.composing) {
        return;
      } // IME対応(日本語変換中のEnterを止める)
      if (e.key === 'Enter') {
        this.pushBtn("SEARCH");
      }
    },
    getCurrentPage(currentPage) {
      let query = Object.assign({}, this.$route.query);
      query["page"] = currentPage;
      this.$router.push({query});
    },
    pushBtn : function (name) {
      if (name == "SEARCH") {
        let query = Object.assign({}, this.$route.query);

        //前と同じかどうか
        if (query["page"] == 1) {
          this.$root.showLoad();
          this.getPostList();
        }
        query["page"] = 1;
        this.$router.push({query});
      } else if (name == "BACK") {
        let sPage = localStorage.getItem("S_PAGE");
        if (sPage == null) {
          sPage = 1;
        }
        this.$router.push({path : "/soudan", query : {page : sPage}});
      }
    },
    editAction : function (params) {
      if (params.kbn == "POST") {
        //投稿成功
        let query = Object.assign({}, this.$route.query);
        if (query["page"] == 1) {
          this.$root.showLoad();
          this.getPostList();
        }
        query["page"] = 1;
        this.$router.push({query});
      }
    },
    rowAction : function (params) {
      if (params.kbn == "NO") {
        let id = params.id;

        this.$refs.edit.responseAction(id);
      }
    },
    getPostList : function () {
      this.$root.pageAction("RESPONSE");

      let params = new URLSearchParams();
      params.append('postKbn', "RESPONSE");
      params.append('page', this.currentPage);
      params.append('soudanId', this.soudanId);
      params.append('userId', this.$root.userId);
      params.append('degree', JSON.stringify(this.$root.dispDegreeList));

      axios.post(this.$root.SERVER_PATH + "getPostList", params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_SUCCESS) {
          //成功
          this.postList = resData["postList"];
          this.totalCount = resData["postCount"];

          this.soudanData = resData["soudanData"];

          var tPage = Math.floor(this.totalCount / this.perPage);
          if (this.totalCount % this.perPage != 0) {
            tPage = tPage + 1;
          }
          this.totalPages = parseInt(tPage);
          // this.currentPage = parseInt(page);

          this.$root.chkUserId(resData);
        }
      });
    },
    beforeAction : function () {
      let query = Object.assign({}, this.$route.query);
      let okFlg = true;

      var qPage = query["page"];
      if (qPage == null || isNaN(parseInt(qPage)) || parseInt(qPage) < 1) {
        qPage = 1;
        okFlg = false;
      }

      if (okFlg == false) {
        //この時点でアウトの場合
        query["page"] = qPage;
        this.$router.replace({query});
      } else {
        this.soudanId = this.$route.params.soudanId;
        this.currentPage = parseInt(qPage);
        this.$refs.edit.initAction();
        this.getPostList();
      }
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.beforeAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.beforeAction();
    });
  },
  watch : {
    '$route'(to) {
      if (to["name"] == "SoudanResponse") {
        this.$root.showLoad(); 
        this.beforeAction();
      }
    }
  }


}
</script>

<style>
.kenBtnNormal {
  text-decoration: none;
  /* color: $colorBtnBase; */
  border-radius: 6px;
  border: solid 1px gray;
  background-color: #fff;
  padding: 5px 10px;
  cursor: pointer;
  box-shadow: 3px 3px 0px 0 rgba(0, 0, 0, 0.2);
  min-width:100px;
  margin: 0 10px;
}

.kenBtnSelect {
  background-color: #F8C5CB !important;
}


</style>