import Top from '../views/Top.vue'
import Top2 from '../views/Top2.vue'
import NotFound from '../views/NotFound.vue'
import Test from '../views/Test.vue'
import Report from '../views/Report.vue'
import Guchi from '../views/Guchi.vue'
import Soudan from '../views/Soudan.vue'
import SoudanResponse from '../views/SoudanResponse.vue'
import Nandemo from '../views/Nandemo.vue'
import Examinee from '../views/Examinee.vue'
import Illust from '../views/Illust.vue'
import Mgr from '../views/Manager.vue'
import Howto from '../views/Howto.vue'

const routes = [
  {
    path: '/',
    name: 'Top',
    component: Top
  },
  {
    path: '/top2',
    name: 'Top2',
    component: Top2
  },
  {
    path: '/howto',
    name: 'Howto',
    component: Howto
  },
  {
    path: '/mgr/:code',
    name: 'Mgr',
    component: Mgr
  },
  {
    path: '/report',
    name: 'Report',
    component: Report
  },
  {
    path: '/illust',
    name: 'Illust',
    component: Illust
  },
  {
    path: '/guchi',
    name: 'Guchi',
    component: Guchi
  },
  {
    path: '/soudan',
    name: 'Soudan',
    component: Soudan
  },
  {
    path: '/response/:soudanId',
    name: 'SoudanResponse',
    component: SoudanResponse
  },
  {
    path: '/nandemo',
    name: 'Nandemo',
    component: Nandemo
  },
  {
    path: '/examinee',
    name: 'Examinee',
    component: Examinee
  },
  {
    path: '/test',
    name: 'Test',
    component: Test
  },
  {
    //無効なURL
    path: "/:pathMatch(.*)*",
    name: "NotFound",
    component: NotFound,
  },
]


// import { VueRouter } from 'vue-router'
// const router = new VueRouter({
//   mode : "history",
//   hash : false,
//   // base:'/myquiz/public/',
//   routes
// })

const scrollBehavior = (to, from, savedPosition) => {
  if (savedPosition) {
    return savedPosition;
  } else {
    return { top: 0, behavior:'instant' }
    // return { x: 0, y: 0 }
  }
};

import { createRouter, createWebHistory } from 'vue-router'
const router = createRouter({
  // history: createWebHistory(),
  history: createWebHistory('/'),
  routes,
  scrollBehavior
})

// import { createRouter, createWebHashHistory } from 'vue-router'
// const router = createRouter({
//   history : createWebHashHistory(),
//   routes
// })

export default router
