<template>
<div id="overlay" v-show="isShowModal" v-on:click="closeOverlay($event)">
  <div id="content" style="text-align:center;" :class="[modalWidth, popupKbn]">
    <div>
      <div class="sp_line_s" />

      <div v-show="this.title != ''">
        <p class="font_l">{{ title }}</p>
      </div>
      
      <div v-show="this.body != ''">
        <div class="sp_line_s" />
        <div style="text-align:center; white-space: pre-line;" class="font_m">{{ this.body }}</div>
      </div>

      <div v-show="popupKbn == 'LIST'">
        <div class="sp_line_s" />

        <div v-show="kbn == 'DELETE'" style="width:100%; margin:auto;">
          <span class="font_l font_b">暗証番号を入力してください</span>

          <div class="sp_line_s" />

          <p class="errMsg font_m" v-show="this.errorMessage != ''">▼ {{this.errorMessage}}</p>
          <input type="text" class="form-control inputBase" style="width:150px; margin:auto" v-model="deleteNo" placeholder="" maxlength="10">
            
          <div class="sp_line_s" />

          <button type="button" v-on:click="pushDelete" class="btnBase" style="width:40%;">削除する</button>
        </div>

        <div v-show="kbn == 'REPORT'" style="width:100%; margin:auto;">
          <span class="font_l font_b">通報内容を入力してください</span>
          <div class="sp_line_m" />
          <p class="errMsg" v-show="this.errorMessage != ''">▼ {{this.errorMessage}}</p>
          <textarea style="width:80%; margin:auto" class="form-control inputBase" rows="3" v-model="reportMessage" placeholder="" maxlength="200"></textarea>
            
          <div class="sp_line_s" />

          <button type="button" v-on:click="pushReport" class="btnBase" style="width:40%;">通報する</button>
        </div>
      </div>
      
      <div v-show="this.kbn == 'CONFIRM'">
        <div class="sp_line_s" />
        <div style="text-align:center">
          <button type="button" v-on:click="pushConfirmOk" class="btnBase" style="width:40%; margin-right:10px">{{ confirmYesLabel }}</button>
          <button type="button" v-on:click="pushConfirmCancel" class="btnBase" style="width:40%; margin-left:10px">キャンセル</button>
        </div>
      </div>
      
      <div v-show="this.kbn == 'DELETE_SUCCESS' || this.kbn == 'COMPLETE' || this.kbn == 'WARNING_POST' || this.kbn == 'POST_SUCCESS' || this.kbn == 'REPORT_SUCCESS' || this.kbn == 'COPY' || this.kbn == 'POST_REPORT_ST_SUCCESS' || this.kbn == 'POST_REPORT_ED_SUCCESS'">
        <div class="sp_line_s" />
        <div style="text-align:center">
          <button type="button" v-on:click="pushPopupBtn" class="btnBase" style="width:40%; margin-right:10px">OK</button>
        </div>
        <div v-show="this.kbn == 'POST_REPORT_ST_SUCCESS'">
          <div class="sp_line_s" />
          トップページのタイマーをスタートしますか？<br>
          現在：{{this.$root.timerStatusLabel}}
          <div class="sp_line_s" />
          <button type="button" v-on:click="pushTimerStartBtn" class="btnBase" style="width:40%; margin-right:10px">タイマースタート</button>
          <p class="font_s">既にタイマーが動いている場合はリセットされてスタートします</p>
        </div>
        <div v-show="this.kbn == 'POST_REPORT_ST_SUCCESS' || this.kbn == 'POST_REPORT_ED_SUCCESS'">
          <div class="sp_line_s" />
          SNSでみんなに共有しませんか？
          <div class="sp_line_s" />
          <button type="button" v-on:click="pushIllustBtn" class="btnBase" style="width:40%; margin-right:10px">「イラスト」に移動</button>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
// import axios from 'axios'
export default {
  name: 'Popup',
  props: {
    popupKbn : String,
  },
  components: {
  },
  data() {
    return {
			kbn : "",
			title : "",
			body : "",
			isShowModal : false,

      deleteNo : "",
      reportMessage : "",

      errorMessage : "",
    }
  },
  created : function () {
  },
  computed : {
		modalWidth () {
			// if (this.kbn == "COPY") {
			// 	return "modalWidth-B";
			// } else {
				return "modalWidth-A";
			// }
		},
  },
  mounted () {
  },
  methods : {
    showModal : function(kbnStr) {
      this.deleteNo = "";
      this.reportMessage = "";

			document.getElementById('body').className = "modalBg";
			this.errorMessage = "";
			this.kbn = kbnStr;
			this.isShowModal = true;
		},
		stopModalEvent: function(event) {
			event.stopPropagation()
		},
		pushConfirmCancel : function () {
			this.closeModal();
		},
		pushDelete : function() {
      this.errorMessage = "";
      if (this.deleteNo == "") {
        this.errorMessage = "暗証番号を入力してください";
        return;
      }
			this.$emit('deleteAction', this.deleteNo);
		},
		pushReport : function() {
      this.errorMessage = "";
      if (this.reportMessage == "") {
        this.errorMessage = "通報内容を入力してください";
        return;
      }
			this.$emit('reportAction', this.reportMessage);
		},
    pushPopupBtn : function () {
      if (this.kbn == "REPORT_SUCCESS") {
        this.closeModal();
        return;
      }

      this.$emit('popupAction', this.kbn);
    },
    pushTimerStartBtn : function () {
      this.$root.restartTimer();
    },
    pushIllustBtn : function () {
      this.$emit('reportPopupAction', 'ILLUST');
    },
		closeModal: function() {
			document.getElementById('body').className = "";
			//モーダルを閉じる
			this.kbn = "";
			this.title = "";
			this.body = "";
			this.isShowModal = false;
		},
		closeOverlay : function (e) {
			if (e.target.id == "overlay") {
				if (this.kbn == "POST_COMPLETE" || this.kbn == "DELETE_COMPLETE") {
					//更新時は無効
					return;
				}

				document.getElementById('body').className = "";
				this.kbn = "";
				this.title = "";
				this.body = "";

				this.isShowModal = false;
			}
		},
    initAction : function () {

    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  watch : {
  }


}
</script>

