<template>
  <div class="Mgr" style="min-height:100vh; text-align:left">
    <div class="sp_line_m" />
    <p class="font_xl">勉強報告</p>
    <div v-for="(data, index) in reportList" :key="index" style="padding:10px; border:1px solid black; margin-top:10px">
      {{data.created_at}}&nbsp;{{data.report_id}}
      <div class="sp_line_ss" />
      {{data.message}}
      <div class="sp_line_ss" />
      <p style="padding:10px; border:1px solid gray">{{data}}</p>
      <div class="sp_line_ss" />
      <button type="button" class="btnBase" v-on:click="pushThrow('REPORT', index)">スルー</button>&nbsp;
      <button type="button" class="btnBase" v-on:click="pushBlack('REPORT', index)">Black</button>
    </div>

    <div class="sp_line_m" />
    
    <p class="font_xl">受験生</p>
    <div v-for="(data, index) in examineeList" :key="index">
      {{data.created_at}}&nbsp;{{data.report_id}}
      <div class="sp_line_ss" />
      {{data.message}}
      <div class="sp_line_ss" />
      <p style="padding:10px; border:1px solid gray">{{data}}</p>
      <div class="sp_line_ss" />
      <button type="button" class="btnBase" v-on:click="pushThrow('EXAMINEE', index)">スルー</button>&nbsp;
      <button type="button" class="btnBase" v-on:click="pushBlack('EXAMINEE', index)">Black</button>
    </div>

    <div class="sp_line_m" />

    <p class="font_xl">愚痴</p>
    <div v-for="(data, index) in guchiList" :key="index">
      {{data.created_at}}&nbsp;{{data.report_id}}
      <div class="sp_line_ss" />
      {{data.message}}
      <div class="sp_line_ss" />
      <p style="padding:10px; border:1px solid gray">{{data}}</p>
      <div class="sp_line_ss" />
      <button type="button" class="btnBase" v-on:click="pushThrow('GUCHI', index)">スルー</button>&nbsp;
      <button type="button" class="btnBase" v-on:click="pushBlack('GUCHI', index)">Black</button>
    </div>

    <div class="sp_line_m" />

    <p class="font_xl">相談</p>
    <div v-for="(data, index) in soudanList" :key="index">
      {{data.created_at}}&nbsp;{{data.report_id}}
      <div class="sp_line_ss" />
      {{data.message}}
      <div class="sp_line_ss" />
      <p style="padding:10px; border:1px solid gray">{{data}}</p>
      <div class="sp_line_ss" />
      <button type="button" class="btnBase" v-on:click="pushThrow('SOUDAN', index)">スルー</button>&nbsp;
      <button type="button" class="btnBase" v-on:click="pushBlack('SOUDAN', index)">Black</button>
    </div>

    <div class="sp_line_m" />

    <p class="font_xl">返信</p>
    <div v-for="(data, index) in responseList" :key="index">
      {{data.created_at}}&nbsp;{{data.report_id}}
      <div class="sp_line_ss" />
      {{data.message}}
      <div class="sp_line_ss" />
      <p style="padding:10px; border:1px solid gray">{{data}}</p>
      <div class="sp_line_ss" />
      <button type="button" class="btnBase" v-on:click="pushThrow('RESPONSE', index)">スルー</button>&nbsp;
      <button type="button" class="btnBase" v-on:click="pushBlack('RESPONSE', index)">Black</button>
    </div>

    <div class="sp_line_m" />

    <p class="font_xl">なんでも</p>
    <div v-for="(data, index) in nandemoList" :key="index">
      {{data.created_at}}&nbsp;{{data.report_id}}
      <div class="sp_line_ss" />
      {{data.message}}
      <div class="sp_line_ss" />
      <p style="padding:10px; border:1px solid gray">{{data}}</p>
      <div class="sp_line_ss" />
      <button type="button" class="btnBase" v-on:click="pushThrow('NANDEMO', index)">スルー</button>&nbsp;
      <button type="button" class="btnBase" v-on:click="pushBlack('NANDEMO', index)">Black</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Mgr',
  components: {
  },
  data() {
    return {
      reportList : [],
      examineeList : [],
      guchiList : [],
      soudanList : [],
      responseList : [],
      nandemoList : [],
    }
  },
  created : function () {
  },
  computed : {

  },
  mounted () {
  },
  methods : {
    pushThrow : function (kbn, index) {
      let val = 0;
      if (kbn == "REPORT") {
        val = this.reportList[index].id;
      } else if (kbn == "EXAMINEE") {
        val = this.examineeList[index].id;
      } else if (kbn == "GUCHI") {
        val = this.guchiList[index].id;
      } else if (kbn == "SOUDAN") {
        val = this.soudanList[index].id;
      } else if (kbn == "RESPONSE") {
        val = this.responseList[index].id;
      } else if (kbn == "NANDEMO") {
        val = this.nandemoList[index].id;
      }
      let params = new URLSearchParams();
      params.append("id", val)
      axios.post(this.$root.SERVER_PATH + "updateThrowMgr", params)
      .then((response) => {
        console.log(response.data);
        this.getViolationList();
      });
    },
    pushBlack : function (kbn, index) {
      let id = 0;
      let userId = "";
      if (kbn == "REPORT") {
        id = this.reportList[index].id;
        userId = this.reportList[index].user_id;
      } else if (kbn == "EXAMINEE") {
        id = this.examineeList[index].id;
        userId = this.examineeList[index].user_id;
      } else if (kbn == "GUCHI") {
        id = this.guchiList[index].id;
        userId = this.guchiList[index].user_id;
      } else if (kbn == "SOUDAN") {
        id = this.soudanList[index].id;
        userId = this.soudanList[index].user_id;
      } else if (kbn == "RESPONSE") {
        id = this.responseList[index].id;
        userId = this.responseList[index].user_id;
      } else if (kbn == "NANDEMO") {
        id = this.nandemoList[index].id;
        userId = this.nandemoList[index].user_id;
      }
      let params = new URLSearchParams();
      params.append("id", id)
      params.append("userId", userId)
      axios.post(this.$root.SERVER_PATH + "updateBlackMgr", params)
      .then((response) => {
        console.log(response.data);
        this.getViolationList();
      });
    },
    getViolationList : function () {
      let params = new URLSearchParams();
      axios.post(this.$root.SERVER_PATH + "getViolationList", params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_SUCCESS) {
          //成功
          this.reportList = resData["reportList"];
          this.examineeList = resData["examineeList"];
          this.guchiList = resData["guchiList"];
          this.soudanList = resData["soudanList"];
          this.responseList = resData["responseList"];
          this.nandemoList = resData["nandemoList"];
          // console.log(this.reportList);
          
          // this.$root.chkUserId(resData);
        }
      });
    },
    initAction : function () {
      let code = this.$route.params.code;

      //チェック
      var date = new Date();
      var year = date.getFullYear();
      var month = date.getMonth() + 1;
      var day = date.getDate();

      let chk = (year - 10) + "-" + ('0' + month).slice(-2) + "-" + ('0' + day).slice(-2);

      if (code != chk) {
        this.$router.push("/");
        return;
      }

      this.getViolationList();
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  watch : {
  }


}
</script>

