<template>
<div class="Top2" style="min-height:100vh">
  <div class="sp_line_s" />
  <p class="font_xl">愚痴</p>
  <div class="sp_line_s" />
  <div class="pageDescription">
    勉強でのストレスや、学校・家庭での出来事など、日頃の愚痴をここで吐き出してしまいましょう。<br>
    スッキリしてまた明日からがんばろう！<br>
    ※個人名や個人情報に関わる投稿はおやめください。また、見る側、コメントする側は寛大な心でお願いします。
  </div>
  <div class="sp_line_s" />
 
  <button type="button" class="btnBase writeBtn" @click="this.$root.autoScroll('EDIT')">書き込む</button>
  
  <ListFrame kbn="GUCHI" :list="this.postList" @rowAction="rowAction"></ListFrame>

  <div class="sp_line_s" />
  
  <div class="dotBar" />

  <subPagenation
    :showPages="showPages"
    :currentPage="currentPage"
    :totalCount="totalCount"
    :perPage="perPage"
    :totalPages="totalPages"
    @currentPage="getCurrentPage"
  ></subPagenation>
  
  <EditFrame ref="edit" kbn="GUCHI" @editAction="editAction"></EditFrame>
</div>
</template>

<script>
import axios from 'axios'
import SubPagenation from './SubPagenation.vue';
import EditFrame from './EditFrame.vue';
import ListFrame from './ListFrame.vue';

export default {
  name: 'Guchi',
  components: {
    SubPagenation,
    EditFrame,
    ListFrame
  },
  data() {
    return {
      //ページネーションの設定
      currentPage: 1, //現在のページ（初期は1）
      showPages: 5, //ページネーションを何ページ表示するか（奇数でないとずれる）
      perPage: 20, //1ページの表示件数
      totalCount: 0, //取得したアイテムの総件数
      totalPages: 0, //総ページ数

      nickName : "",
      message : "",
      deleteSecret : "",

      errMsg : "",
      postList : [],

      targetReportId : "",
    }
  },
  created : function () {
  },
  computed : {
  },
  mounted () {
  },
  methods : {
    handleKeydown(e) {
      // this.chValue('OUTLINE')
      if (this.composing) {
        return;
      } // IME対応(日本語変換中のEnterを止める)
      if (e.key === 'Enter') {
        this.pushBtn("SEARCH");
      }
    },
    getCurrentPage(currentPage) {
      let query = Object.assign({}, this.$route.query);
      query["page"] = currentPage;
      this.$router.push({query});
    },
    pushBtn : function (name) {
      if (name == "SEARCH") {
        let query = Object.assign({}, this.$route.query);

        //前と同じかどうか
        if (query["page"] == 1) {
          this.$root.showLoad();
          this.getPostList();
        }
        query["page"] = 1;
        this.$router.push({query});
      }
    },
    editAction : function (params) {
      if (params.kbn == "POST") {
        //投稿成功
        let query = Object.assign({}, this.$route.query);
        if (query["page"] == 1) {
          this.$root.showLoad();
          this.getPostList();
        }
        query["page"] = 1;
        this.$router.push({query});
      }
    },
    rowAction : function (params) {
      if (params.kbn == "STAMP") {
        let id = params.id;

        for (let i = 0; i < this.postList.length; i++) {
          if (this.postList[i].id == id) {
            if (this.postList[i].stamp_count == null) {
              this.postList[i].stamp_count = 1;
            } else {
              this.postList[i].stamp_count = parseInt(this.postList[i].stamp_count) + 1;
            }
            this.postList[i].my_flg = "A";
          }
        }
      } else if (params.kbn == "NO") {
        let id = params.id;

        this.$refs.edit.responseAction(id);
      }
    },
    getPostList : function () {
      this.$root.pageAction("GUCHI");

      let params = new URLSearchParams();
      params.append('postKbn', "GUCHI");
      params.append('page', this.currentPage);
      params.append('userId', this.$root.userId);
      params.append('degree', JSON.stringify(this.$root.dispDegreeList));
      axios.post(this.$root.SERVER_PATH + "getPostList", params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_SUCCESS) {
          //成功
          this.postList = resData["postList"];
          this.totalCount = resData["postCount"];

          var tPage = Math.floor(this.totalCount / this.perPage);
          if (this.totalCount % this.perPage != 0) {
            tPage = tPage + 1;
          }
          this.totalPages = parseInt(tPage);
          // this.currentPage = parseInt(page);

          this.$root.chkUserId(resData);
        }
      });
    },
    beforeAction : function () {
      let query = Object.assign({}, this.$route.query);
      let okFlg = true;

      var qPage = query["page"];
      if (qPage == null || isNaN(parseInt(qPage)) || parseInt(qPage) < 1) {
        qPage = 1;
        okFlg = false;
      }

      if (okFlg == false) {
        //この時点でアウトの場合
        query["page"] = qPage;
        this.$router.replace({query});
      } else {
        this.currentPage = parseInt(qPage);

        this.$refs.edit.initAction();
        this.getPostList();
      }
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.beforeAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.beforeAction();
    });
  },
  watch : {
    '$route'(to) {
      if (to["name"] == "Guchi") {
        this.$root.showLoad(); 
        this.beforeAction();
      }
    }
  }


}
</script>

