.kenBtnNormal {
  text-decoration: none;
  /* color: $colorBtnBase; */
  border-radius: 6px;
  border: solid 1px gray;
  background-color: #fff;
  padding: 5px 10px;
  cursor: pointer;
  box-shadow: 3px 3px 0px 0 rgba(0, 0, 0, 0.2);
  min-width:100px;
  margin: 0 10px;
}

.kenBtnSelect {
  background-color: #F8C5CB !important;
}


