    <div style="display:flex !important; flex-wrap:wrap; gap:10px">
      <button type="button" class="btnBase menuBtn font_m" :class="degreeBtnClass(0)" style="min-width:100px" v-on:click="pushDegree(1, 0)">指定なし</button>
      <button type="button" class="btnBase menuBtn font_m" :class="degreeBtnClass(1)" style="min-width:100px" v-on:click="pushDegree(1, 1)">中学生</button>
      <button type="button" class="btnBase menuBtn font_m" :class="degreeBtnClass(2)" style="min-width:100px" v-on:click="pushDegree(1, 2)">高校生</button>
      <button type="button" class="btnBase menuBtn font_m" :class="degreeBtnClass(3)" style="min-width:100px" v-on:click="pushDegree(1, 3)">大学生</button>
      <button type="button" class="btnBase menuBtn font_m" :class="degreeBtnClass(4)" style="min-width:100px" v-on:click="pushDegree(1, 4)">社会人</button>
      <button type="button" class="btnBase menuBtn font_m" :class="degreeBtnClass(5)" style="min-width:100px" v-on:click="pushDegree(1, 5)">その他</button>
