    <div id="content" style="text-align:center; width:90%; max-width: 700px; margin:auto">
      <p v-show="this.listRowData == null" class="font_m">表示するデータがないか、非表示になっています</p>
      <ListRow ref="popupRow" v-show="this.listRowData != null" :kbn="listRowKbn" :data="listRowData" isRes="true"
        @pushResponse="pushResponse"></ListRow>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { Slide } from 'vue3-burger-menu'
import ListRow from './views/ListRow.vue';
  
// 相談・質問の省略と展開
// 文字のフィルタリング
// レス表示

// SNS関連？確認
// おしらせ？
// サーバーアップ

  export default {
    components : {
      Slide,
      ListRow,
    },
    data() {
      return {
        API_SUCCESS : 200,
        API_FAILED_LOGIN : 99,
        API_FAILED_AUTH : 100,
        API_FAILED_PARAM : 300,
        API_FAILED_NODATA : 400,
        API_FAILED_DUPLICATE : 401,
        API_FAILED_MISMATCH : 402,
        API_FAILED_PRIVATE : 500,
        API_FAILED_FILE : 600,
        API_FAILED_EXEPTION : 900,

        IMG_PATH : "/app/img/", 
        ICON_PATH : "/storage/icon/",
        CARD_PATH : "/storage/card/",
        
        BASE_URL : "http://localhost:8001/",
        SERVER_PATH : "http://localhost:8001/api/",
        ROOT_PATH : "/",
        DOMAIN_PATH : "localhost",
        
        // BASE_URL : "https://studytimer.tarolab.jp/",
        // SERVER_PATH : "https://studytimer.tarolab.jp/api/", 
        // ROOT_PATH : "/",
        // DOMAIN_PATH : "studytimer.tarolab.jp",
        
        pageName : "",
        reloadValue : "", //画像の更新用,

        isPopup: false,
        popupKbn: "",
        popupTitle: "",
        popupBody: "",
        popupBodyClass : "popupBodyLeft",

        abList : null,
        abIndexList : [
          {kbn : "GROUP_1", url : ""},
          {kbn : "GROUP_1", url : ""},
          {kbn : "GROUP_1", url : ""},
          {kbn : "GROUP_1", url : ""},
        ],

        allKenList : ["指定なし","北海道","青森県","岩手県","宮城県","秋田県","山形県","福島県","茨城県","栃木県","群馬県","埼玉県","千葉県","東京都","神奈川県","新潟県","富山県","石川県","福井県","山梨県","長野県","岐阜県","静岡県","愛知県","三重県","滋賀県","京都府","大阪府","兵庫県","奈良県","和歌山県","鳥取県","島根県","岡山県","広島県","山口県","徳島県","香川県","愛媛県","高知県","福岡県","佐賀県","長崎県","熊本県","大分県","宮崎県","鹿児島県","沖縄県","海外"],

        isMaking : false,

        userId : "",
        degree : 0,
        dispDegreeList : [],
        status : "INIT",
      
        baseParentWidth : 1000,
        startTime : null,
        timerValue : 0,
        currentTime : 0,

        editFormData : null,
        
        isMenuOpen : false,
        isSettingOpen : false,

        isDegreeChange : false,

        isShowListRow : false,
        listRowKbn : "",
        listRowData : null,
        listRowList : null,
      }
    },
    computed : {
      timerStatusLabel : function () {
        if (this.status == "INIT") { return "未起動"; }
        else if (this.status == "START") { return "起動中"; }
        else if (this.status == "PAUSE") { return "一時停止中"; }
        else if (this.status == "STOP") { return "停止中"; }
        return "";
      },
      catlinkClass : function () {
        return (catName) => {
          if (catName == this.pageName) {
            return "";
          } else {
            return "link";
          }
        }
      },
      myDegreeBtnClass : function () {
        return (index) => {
          if (index == this.degree) {
            return "degreeBtnActive";
          } else {
            return "";
          }
        }
      },
      degreeBtnClass : function () {
        return (index) => {
          for (let i = 0; i < this.dispDegreeList.length; i++) {
            if (index == this.dispDegreeList[i]) {
              return "degreeBtnActive";
            }
          }
          return "";
        }
      },
      kenLabel : function () {
        return (index) => {
          return this.$root.allKenList[index];
        }
      },
      degreeLabel : function () {
        return (val) => {
          val = parseInt(val);
          if (val == 0) {
            return "指定なし";
          } else if (val == 1) {
            return "中学生";
          } else if (val == 2) {
            return "高校生";
          } else if (val == 3) {
            return "大学生";
          } else if (val == 4) {
            return "社会人";
          } else if (val == 5) {
            return "その他";
          }
          return "";
        }
      },
      userIdLabel : function () {
        return (userId) => {
          if (userId == null || userId.length < 6) {
            return "";
          } else {
            return "(" + userId.substring(0, 5) + ")";
          }
        }
      },
    },
    created : function() {
      console.log("a");
      window.addEventListener('beforeinstallprompt', e => {
          console.log("INNNNN");
          e.preventDefault();
          this.promptEvent = e;
      });

      //広告情報取得
      axios.get(this.$root.SERVER_PATH + 'getAbs')
      .then((response) => {
        // console.log(response.data);
        this.abList = response.data["abs"];
        // this.pageAction();
      });
      
      // localStorage.removeItem("USER_ID");
      // localStorage.removeItem("CURRENT_SEC");
      // localStorage.removeItem("START_TIME");
      // localStorage.removeItem("STATUS");

      //学位
      this.degree = parseInt(localStorage.getItem("MY_DEGREE") || "0");

      let dispDegreeStr = localStorage.getItem("DISP_DEGREE") || "";
      this.dispDegreeList = [];
      if (dispDegreeStr != "") {
        this.dispDegreeList = JSON.parse(dispDegreeStr);
      }
      for (let i = 0; i < this.dispDegreeList.length; i++) {
        this.dispDegreeList[i] = parseInt(this.dispDegreeList[i]);
      }

      this.userId = localStorage.getItem("USER_ID") || "";
      
      // localStorage.removeItem("START_TIME");
      // localStorage.removeItem("CURRENT_SEC");
      // localStorage.removeItem("STATUS");

      //前回までの状態を取得
      this.status = localStorage.getItem("STATUS") || "INIT";
      let cTime = localStorage.getItem("CURRENT_SEC") || 0;
      if (cTime != null) {
        this.currentTime = parseInt(cTime);
      } else {
        this.currentTime = 0;
      }
      
      let startStr = localStorage.getItem("START_TIME");
      if (startStr != null) {
        this.startTime = new Date(startStr);
      } else {
        this.startTime = null;
      }

      if (this.status == "STOP" || this.status == "MAKE") {
        this.cHour = Math.floor(this.currentTime / 3600);
        this.cMinute = Math.floor((this.currentTime % 3600) / 60);
        this.cSecond = this.currentTime % 60;
      }
      
      if (this.startTime != null) {
        this.timerValue = Math.floor((new Date() - this.startTime) / 1000) + this.currentTime;
      } else {
        this.timerValue = this.currentTime;
      }

      const countUp = function(){
        if (this.status == "START") {
          let val = 0;
          if (this.startTime != null) {
            val = Math.floor((new Date() - this.startTime) / 1000);
          }
          this.timerValue = val + this.currentTime;
        }
      }.bind(this);
      setInterval(countUp, 1000);
    },
    mounted () {
      this.baseParentWidth = document.getElementById('baseParent').clientWidth;
      this.cardBaseScale = this.baseParentWidth * 0.95 * 0.95 / 600.0;
      if (this.cardBaseScale > 1) {
        this.cardBaseScale = 1.0;
      }
      window.addEventListener('resize', this.calculateWindowWidth);
    },
    methods : {
      closeOverlay : function (e) {
        if (e.target.id == "overlay") {
          this.isShowListRow = false;
        }
      },
      pushCatBtn : function (name) {
        this.isMenuOpen = false;
        if (name == "HOME") {
          this.$router.push("/top2");
        } else if (name == "REPORT") {
          this.$router.push("/report");
        } else if (name == "GUCHI") {
          this.$router.push("/guchi");
        } else if (name == "SOUDAN") {
          this.$router.push("/soudan");
        } else if (name == "NANDEMO") {
          this.$router.push("/nandemo");
        } else if (name == "EXAMINEE") {
          this.$router.push("/examinee");
        } else if (name == "ILLUST") {
          this.$router.push("/illust");
        } else if (name == "HOWTO") {
          this.$router.push("/howto");
        }
      },
      calculateWindowWidth : function () {
        this.baseParentWidth = document.getElementById("baseParent").clientWidth;
        this.cardBaseScale = this.baseParentWidth * 0.95 * 0.95 / 600.0;
        if (this.cardBaseScale > 1) {
          this.cardBaseScale = 1.0;
        }
      },
      pushDegree : function (kbn, index) {
        if (kbn == 0) {
          //自分の学位
          this.degree = index;
          localStorage.setItem("MY_DEGREE", this.degree);
        } else if (kbn == 1) {
          this.isDegreeChange = true;

          //表示学位
          let delFlg = false;
          for (let i = 0; i < this.dispDegreeList.length; i++) {
            if (this.dispDegreeList[i] == index) {
              //削除
              this.dispDegreeList.splice(i, 1);
              delFlg = true;
              break;
            }
          }
          if (!delFlg) {
            this.dispDegreeList.push(index);
          }
          localStorage.setItem("DISP_DEGREE", JSON.stringify(this.dispDegreeList));
        }
      },
      chkInput : function (name, isReq, length, val) {
        if (isReq && val == "") {
          return name + "が未入力です"
        } else if (val.length > length) {
          return name + "は" + length + "文字以内で入力してください";
        }
        
        return "";
      },
      chkUserId : function (resData) {
        if (this.userId == "") {
          return;
        }

        if (resData["userId"] != null) {
          //生成された場合
          localStorage.setItem("USER_ID", resData["userId"]);
          this.userId = resData["userId"];
        }
      },
      pushBtn : function (name) {
        if (name == "TERMS") {
          this.$router.push("/terms");
        } else if (name == "PRIVACY") {
          this.$router.push("/privacypolicy");
        } else if (name == "TWITTER") {
          window.open("https://twitter.com/taroLab2023");
        } else if (name == "MENU") {
          this.isMenuOpen = true;
          this.isSettingOpen = false;
          this.isDegreeChange = false;
        } else if (name == "SETTING") {
          this.isSettingOpen = true;
          this.isMenuOpen = false;
        }
      },
      restartTimer : function () {
        //開始
        this.$root.calculateWindowWidth();
        this.$root.startTime = new Date();
        this.$root.currentTime = 0;
        this.$root.timerValue = 0;
        localStorage.setItem("START_TIME", this.startTime.toLocaleString());
        localStorage.setItem("CURRENT_SEC", 0);
        localStorage.setItem("STATUS", "START");
        this.status = "START";
      },
      autoScroll : function(kbn) {
        if (kbn == "TOP") {
          document.getElementById("scTop").scrollIntoView({  
            behavior: 'smooth'
          });
        } else if (kbn == "EDIT") {
          document.getElementById("editParent").scrollIntoView({  
            block : 'start',
            behavior: 'smooth',
          });
        } else if (kbn == "LISTEND") {
          document.getElementById("listEnd").scrollIntoView({  
            behavior: 'smooth',
            block : 'center'
          });
        }
      },
      showLoad: function() {
        this.showPopup("LOAD", "", "");
      },
      showPopup: function(kbn, title, body) {
        document.getElementById('body').className = "modalBg";
        this.popupKbn = kbn;
        this.popupTitle = title;
        this.popupBody = body;
        this.isPopup = true;
      },
      pushPopupBg : function () {
        if (this.popupKbn == "TIPS") {
          document.getElementById('body').className = "";
          this.isPopup = false;
        }        
      },
      closePopup : function () {
        document.getElementById('body').className = "";
        this.isPopup = false;
      },
      pushHome : function () {
        if (this.$root.pageName == "Top") {
          location.reload();
        } else {
          this.$router.push("/");
        }
      },
      editFormAction : function (kbn) {
        if (kbn == "INIT") {
          if (this.$root.editFormData == null) {
            //初期読み込み
            let formStr = localStorage.getItem("EDIT_DATA") || "";
            if (formStr == "") {
              //デフォルト設定
              this.editFormData = {
                nickName : "",
                deleteSecret : "",
                //受験
                ken : 0,
                choice : "",
                //勉強報告
                title : "",
                message : "",
              };
              let formStr = JSON.stringify(this.editFormData);
              localStorage.setItem("EDIT_DATA", formStr);
            } else {
              this.editFormData = JSON.parse(formStr);
              this.editFormData.ken = parseInt(this.editFormData.ken);
            }
          }
        } else if (kbn == "SAVE") {
          let formStr = JSON.stringify(this.editFormData);
          localStorage.setItem("EDIT_DATA", formStr);
        }
      },
      pageAction : function (pageName) {
        this.autoScroll("TOP");
        // if (this.abList == null) {
        //   return;
        // }
        // let ranList = [];

        // for (let i = 0; i < this.abIndexList.length; i++) {
        //   let data = this.abIndexList[i];
        //   let kbn = data["kbn"];

        //   let targetList = this.abList[kbn];
        //   let ran = -1;
        //   while (ran == -1) {
        //     ran = Math.floor(Math.random() * targetList.length);

        //     for (let j = 0; j < ranList.length; j++) {
        //       if (ranList[j] == ran) {
        //         ran = -1;
        //         break;
        //       }
        //     }
        //   }
        //   ranList.push(ran);
        //   // console.log(ranList);
        //   data["url"] = targetList[ran];
        // }
        if (pageName != "") {
          this.pageName = pageName;
        }
      },
      pushMenuClose : function () {
        this.isMenuOpen = false;
      },
      pushSettingClose : function () {
        this.isSettingOpen = false;
        if (this.isDegreeChange) {
          this.$router.go({path: this.$router.currentRoute.path, force: true});
        }
      },
      getListRowData : function (id, kbn) {
        let params = new URLSearchParams();
        params.append('postKbn', kbn);
        params.append('page', -2);
        params.append('keyword', id);
        params.append('userId', this.$root.userId);
        params.append('degree', JSON.stringify(this.$root.dispDegreeList));

        axios.post(this.$root.SERVER_PATH + "getListRowData", params)
        .then((response) => {
          let resData = response.data;
          this.listRowKbn = kbn;
          if (resData["postData"] == null) {
            //対象データなし
            this.listRowData = null;
            this.isShowListRow = true;
          } else {
            this.listRowData = resData["postData"];
            this.$refs.popupRow.alignBody();
            this.isShowListRow = true;
          }
        });
      },
      pushResponse : function (id) {
        this.listRowAction(id, this.listRowList, this.listRowKbn);
      },
      listRowAction : function (id, list, kbn) {
        //現在の一覧にあるか
        let targetData = null;
        for (let i = 0; i < list.length; i++) {
          if (id == list[i].id) {
            targetData = list[i];
            break;
          }
        }

        this.listRowList = list;

        // this.$root.listRowKbn = this.kbn;
        if (targetData == null) {
          //取得しに行く
          this.$root.getListRowData(id, kbn);
        } else {
          this.listRowKbn = kbn;
          this.listRowData = targetData;
          this.$refs.popupRow.alignBody();
          this.isShowListRow = true;
        }
      },
      alignIllustMessage : function (message) {
        let ngWords = ["えっち", "エッチ", "エロ", "下着", "援助", "援交", "P活", "パパ活", "ちんこ", "チンコ", "まんこ", "せっくす", "セックス", "せふれ", "セフレ", "うんこ", "ウンコ", "変態", "SEX", "円光", "アダルト", "勃起", "おちんちん", "あなる", "せっくす", "ヤリマン", "ザーメン", "おっぱい", "オッパイ", "チンポ", "巨乳", "巨根", "性欲", "ホ別", "穂別", "エロイプ", "えろいぷ", "見せ合い", "見せあい", "不倫", "浮気", "チン凸", "マン凸", "おなにー", "オナニー", "おほ声", "オホ声", "オナ電", "おな電", "会い", "ホテル", "ドM", "ドS", "アソコ", "あそこ", "ムラムラ", "フェラ", "カカオ", "乳首"];

        for (let i = 0; i < ngWords.length; i++) {
          const word = ngWords[i];
          message = message.replace(new RegExp(word, "g"), "***");
        }
        return message;
      }
    }
  }
</script>

