<template>
<div class="Top2" style="min-height:100vh">
  <div class="sp_line_s" />
  <p class="font_xl">受験生</p>
  <div class="sp_line_s" />
  <div class="pageDescription">
    受験生同士で情報を共有・交換していきましょう！<br>
    都道府県で絞ることができるので、同じ県内の人と志望校の話をしたり、決意表明したり、オープンキャンパスの感想などを教えてあげるのもいいと思います。<br>
  </div>
  <div class="sp_line_s" />

  <button type="button" class="btnBase writeBtn" @click="this.$root.autoScroll('EDIT')">書き込む</button>
  
  <div class="sp_line_s" />
  
  <div class="searchBox" style="width:100%; margin:auto">
    <input type="text" class="inputBase searchBox-text" v-model="keyword" style="width:100%" placeholder="検索ワードを入力してください" @keydown="handleKeydown" autocomplete="off">
    <img :src="this.$root.IMG_PATH + 'btn_search.png'" class="searchBox-btn" v-on:click="pushBtn('SEARCH')" style="width:22px; height:22px">
  </div>

  <div class="sp_line_xl" />

  都道府県選択：
  <div class="sp_line_ss" />
  <div v-show="!isOpenKen">
    <p v-show="this.kenList.length == 0">-- 選択なし --</p>
    <p v-show="this.kenList.length == this.$root.allKenList.length">-- すべて --</p>
    <div v-show="this.kenList.length != this.$root.allKenList.length" style="display:flex; gap:5px 5px; justify-content:center; flex-wrap: wrap;">
      <span v-for="(kenIndex, index) in kenList" style="white-space: nowrap" :key="index">{{ this.$root.kenLabel(kenIndex) }}</span>
    </div>
    <div class="sp_line_ss" />
    <button type="button" class="btnBase" v-on:click="pushBtn('KEN_OPEN')">都道府県選択</button>
  </div>
  <div v-show="isOpenKen">
    <div style="display:flex; gap:10px 10px; flex-wrap: wrap;">
      <span v-for="(ken, index) in this.$root.allKenList" :key="index" class="kenBtnNormal" :class="selKenClass(index)" v-on:click="pushKen(index)">{{ken}}</span>
    </div>
    <div class="sp_line_ss" />
    <button type="button" class="btnBase" v-on:click="pushBtn('KEN_CLOSE')">閉じる</button>&nbsp;
    <button type="button" class="btnBase" v-on:click="pushBtn('KEN_ALL')">全て選択</button>&nbsp;
    <button type="button" class="btnBase" v-on:click="pushBtn('KEN_NONE')">全て選択解除</button>
  </div>

  <div class="sp_line_m" />

  <ListFrame kbn="EXAMINEE" :list="this.postList" @rowAction="rowAction"></ListFrame>

  <div class="sp_line_s" />
  
  <div class="dotBar" />

  <subPagenation
    :showPages="showPages"
    :currentPage="currentPage"
    :totalCount="totalCount"
    :perPage="perPage"
    :totalPages="totalPages"
    @currentPage="getCurrentPage"
  ></subPagenation>

  <EditFrame ref="edit" kbn="EXAMINEE" @editAction="editAction"></EditFrame>
</div>
</template>

<script>
import axios from 'axios'
import SubPagenation from './SubPagenation.vue';
import EditFrame from './EditFrame.vue';
import ListFrame from './ListFrame.vue';

export default {
  name: 'Examinee',
  components: {
    SubPagenation,
    EditFrame,
    ListFrame
  },
  data() {
    return {
      //ページネーションの設定
      currentPage: 1, //現在のページ（初期は1）
      showPages: 5, //ページネーションを何ページ表示するか（奇数でないとずれる）
      perPage: 20, //1ページの表示件数
      totalCount: 0, //取得したアイテムの総件数
      totalPages: 0, //総ページ数

      kenList : [],

      isOpenKen : false,
      kenStr : "",

      nickName : "",
      message : "",
      choice : "",
      ken : 0,
      deleteSecret : "",

      errMsg : "",
      postList : null,

      targetReportId : "",

      curKenStr : "",
    }
  },
  created : function () {
  },
  computed : {
    selKenClass : function () {
      return (index) => {
        for (let i = 0; i < this.kenList.length; i++) {
          if (this.kenList[i] == index) {
            return "kenBtnSelect";
          }
        }
        return "";
      }
    }
  },
  mounted () {
  },
  methods : {
    handleKeydown(e) {
      // this.chValue('OUTLINE')
      if (this.composing) {
        return;
      } // IME対応(日本語変換中のEnterを止める)
      if (e.key === 'Enter') {
        this.pushBtn("SEARCH");
      }
    },
    getCurrentPage(currentPage) {
      let query = Object.assign({}, this.$route.query);
      query["page"] = currentPage;
      this.$router.push({query});
    },
    saveKen : function () {
      //設定内容を保存
        let saveStr = "";
        for (let i = 0; i < this.kenList.length; i++) {
          saveStr += this.kenList[i];

          if (i != this.kenList.length - 1) {
            saveStr += ",";
          }
        }
        localStorage.setItem("SELECT_KEN", saveStr);
        this.kenStr = saveStr;
    },
    pushBtn : function (name) {
      if (name == "KEN_OPEN") {
        this.kenList.sort();
        this.curKenStr = JSON.stringify(this.kenList);
        this.isOpenKen = true;
      } else if (name == "KEN_CLOSE") {

        this.kenList.sort();
        let kenStr = JSON.stringify(this.kenList);
        if (this.curKenStr != kenStr) {
          this.getPostList();
        }

        this.isOpenKen = false;
      } else if (name == "KEN_ALL") {
        this.kenList = [];
        for (let i = 0; i < this.$root.allKenList.length; i++) {
          this.kenList.push(i);
        }
        this.saveKen();
      } else if (name == "KEN_NONE") {
        this.kenList = [];
        this.saveKen();
      } else if (name == "SEARCH") {
        let query = Object.assign({}, this.$route.query);

        //前と同じかどうか
        if (query["page"] == 1 && query["keyword"] == this.keyword) {
          this.$root.showLoad();
          this.getPostList();
        }
        query["page"] = 1;
        query["keyword"] = this.keyword;
        this.$router.push({query});
      }
    },
    editAction : function(params) {
      if (params.kbn == "POST") {
        //投稿成功
        let query = Object.assign({}, this.$route.query);
        if (query["page"] == 1 && query["keyword"] == this.keyword) {
          this.$root.showLoad();
          this.getPostList();
        }
        query["page"] = 1;
        query["keyword"] = this.keyword
        this.$router.push({query});
      }
    },
    rowAction : function (params) {
      if (params.kbn == "STAMP") {
        let id = params.id;

        for (let i = 0; i < this.postList.length; i++) {
          if (this.postList[i].id == id) {
            if (this.postList[i].stamp_count == null) {
              this.postList[i].stamp_count = 1;
            } else {
              this.postList[i].stamp_count = parseInt(this.postList[i].stamp_count) + 1;
            }
            this.postList[i].my_flg = "A";
          }
        }
      } else if (params.kbn == "NO") {
        let id = params.id;

        this.$refs.edit.responseAction(id);
      }
    },
    pushKen : function (index) {
      for (let i = 0; i < this.kenList.length; i++) {
        if (index == this.kenList[i]) {
          //削除
          this.kenList.splice(i, 1);
          this.kenList.sort();
          this.saveKen();
          return;
        }
      }
      //追加
      this.kenList.push(index);
      this.kenList.sort();
      this.saveKen();
    },
    getPostList : function () {
      this.$root.pageAction("EXAMINEE");

      let params = new URLSearchParams();
      params.append('postKbn', "EXAMINEE");
      params.append('keyword', this.keyword);
      params.append('page', this.currentPage);
      params.append('userId', this.$root.userId);
      params.append('kens', this.kenStr);
      params.append('degree', JSON.stringify(this.$root.dispDegreeList));
      axios.post(this.$root.SERVER_PATH + "getPostList", params)
      .then((response) => {
        let resData = response.data;
        if (resData["status"] == this.$root.API_SUCCESS) {
          //成功
          this.postList = resData["postList"];
          this.totalCount = resData["postCount"];

          var tPage = Math.floor(this.totalCount / this.perPage);
          if (this.totalCount % this.perPage != 0) {
            tPage = tPage + 1;
          }
          this.totalPages = parseInt(tPage);
          // this.currentPage = parseInt(page);

          this.$root.chkUserId(resData);
        }
      });
    },
    beforeAction : function () {
      let query = Object.assign({}, this.$route.query);
      let okFlg = true;

      var qPage = query["page"];
      if (qPage == null || isNaN(parseInt(qPage)) || parseInt(qPage) < 1) {
        qPage = 1;
        okFlg = false;
      }
      
      var qKeyword = query["keyword"];
      if (qKeyword == null) {
        qKeyword = "";
        okFlg = false;
      }

      if (okFlg == false) {
        //この時点でアウトの場合
        query["page"] = qPage;
        query["keyword"] = qKeyword;
        this.$router.replace({query});
      } else {
        this.currentPage = parseInt(qPage);
        this.keyword = qKeyword;

        let kenStr = localStorage.getItem("SELECT_KEN");
        this.kenStr = kenStr;

        this.kenList = [];
        if (kenStr == null) {
          for (let i = 0; i < this.$root.allKenList.length; i++) {
            this.kenList.push(i);
          }
        } else if (kenStr != "") {
          let kenArray = kenStr.split(",");
          for (let i = 0; i < kenArray.length; i++) {
            this.kenList.push(parseInt(kenArray[i]));
          }
        }

        this.$refs.edit.initAction();
        this.getPostList();
      }
    },
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.beforeAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.$root.showLoad();
      vm.beforeAction();
    });
  },
  watch : {
    '$route'(to) {
      if (to["name"] == "Examinee") {
        this.$root.showLoad(); 
        this.beforeAction();
      }
    }
  }


}
</script>

<style>
.kenBtnNormal {
  text-decoration: none;
  /* color: $colorBtnBase; */
  border-radius: 6px;
  border: solid 1px gray;
  background-color: #fff;
  padding: 5px 10px;
  cursor: pointer;
  box-shadow: 3px 3px 0px 0 rgba(0, 0, 0, 0.2);
  min-width:100px;
  margin: 0 10px;
}

.kenBtnSelect {
  background-color: #F8C5CB !important;
}


</style>