<template>
<div id="baseParent" class="top">
  <!-- <div class="sp_line_ss" />
  <iframe src='https://d830x8j3o1b2k.cloudfront.net/adfurikun/api/get-script/locale/ja/app_id/647e57ee38396d73e85f9099/ssl/1/iframe/1' width='320' height='50' style='border:none;margin:0 auto;padding:0;display:block;'></iframe> -->

  <div class="sp_line_ss" />
  <p class="font_xl" style="text-align:center">あなたのがんばりをみんなにおしらせしましょう♪</p>
  <div class="sp_line_s" />

  <div v-show="!isHowto" style="text-align:left">
    <img :src="this.$root.IMG_PATH + 'icon_howto.png'" style="width:20px; margin-left:10px"><span class="link" v-on:click="pushHowto">『まなんだー』とは？</span>
  </div>
  <div v-show="isHowto" class="howtoParent" style="width:95%; margin:auto; text-align:left; pointer:cursor" v-on:click="pushHowto">
    <span class="font_l font_b">『まなんだー』とは？</span>
    <div class="sp_line_s" />
    高校大学受験や資格取得の勉強、学校の課題などをするときのタイマーとしてご利用ください。<br>
    開始と終了時にTwitterでツイートすることで画像付きでフォロワーに報告することができます。<br>
    また、画像として保存することで、他のSNSへの添付や勉強の記録として残すこともできます。<br>
  </div>
  
  <div class="sp_line_xl" />
  <div>
    <!-- <img :src="this.$root.IMG_PATH + 'bg_timer.png'" style="width:100%; max-width:600px; margin:auto"> -->
    <span class="timerParent" style="font-family: 'M PLUS Rounded 1c';" :style="timerFontSize">{{timerLabel}}</span>
  </div>
  <div class="sp_line_xl" />
  <div>
    <div v-show="status == 'INIT'">
      <span class="font_xl">ボタンをおして勉強を開始してください。</span>
      <!-- <img :src="this.$root.IMG_PATH + 'img_start.png'" style="width:80%; margin:auto"> -->
      <div class="sp_line_m" />
      <button class="btnBase" style="width:150px" v-on:click="pushStatus('START')">勉強を開始する</button>
    </div>
    <div v-show="status == 'START'">
      <span class="font_xl">お勉強がんばってください！</span>
      <!-- <img :src="this.$root.IMG_PATH + 'img_play.png'" style="width:80%; margin:auto"> -->
      <div class="sp_line_m" />
      <button class="btnBase" v-on:click="pushStatus('PAUSE')">休憩する</button>
      <button class="btnBase" v-on:click="pushStatus('STOP')">勉強を終了する</button>
    </div>
    <div v-show="status == 'PAUSE'">
      <span class="font_xl">休憩もたいせつです！</span>
      <!-- <img :src="this.$root.IMG_PATH + 'img_pause.png'" style="width:80%; margin:auto"> -->
      <div class="sp_line_m" />
      <button class="btnBase" v-on:click="pushStatus('RESUME')">再開する</button>
      <button class="btnBase" v-on:click="pushStatus('STOP')">勉強を終了する</button>
    </div>
    <div v-show="status == 'STOP'">
      <span class="font_xl">おつかれさまでした！</span>
      <!-- <img :src="this.$root.IMG_PATH + 'img_stop.png'" style="width:80%; margin:auto"> -->
      <div class="sp_line_m" />
      <button class="btnBase" v-on:click="pushStatus('RESET')">リセットする</button>
    </div>
    
    <div class="sp_line_m" />
    
    <iframe src='https://d830x8j3o1b2k.cloudfront.net/adfurikun/api/get-script/locale/ja/app_id/64862bd80544bf562579acde/ssl/1/iframe/1' width='320' height='50' style='border:none;margin:0 auto;padding:0;display:block;'></iframe>
    
    <div class="sp_line_l" />
    
    <div v-show="status != 'INIT'" class="cardParent" style="width:95%; max-width:650px; margin:auto;">
      <img :src="this.$root.IMG_PATH + labelArray[0]" style="width:70%; margin:auto">
      <div class="sp_line_s" />
      <div class="dotBar" />
      <div class="sp_line_m" />

      <img :src="this.$root.IMG_PATH + 'icon_midashi1.png'" style="width:250px">
      <!-- <span class="stepNo font_xl">1</span><span class="font_b font_xl">タイトルを設定しましょう</span> -->
      <div class="sp_line_m" />
      <input type="text" class="form-control inputBase" maxlength="50" style="width:70%; max-width:600px; margin:auto" placeholder="例）〇〇大学受験、学校の課題、〇〇資格取得"
      v-model="titleName" @change="chTitle">
      <div class="sp_line_s" />
      <span class="font_xl">{{labelArray[1]}}</span>

      <div class="sp_line_l" />
      <div class="dotBar" />
      <div class="sp_line_m" />

      <img :src="this.$root.IMG_PATH + 'icon_midashi2.png'" style="width:250px">
      <!-- <span class="stepNo font_xl">2</span><span class="font_b font_xl">フレームを選択しましょう</span> -->
      <div class="sp_line_m" />
      <span v-show="isImg(frameList[selectedFrame], 0)" class="demo" :class="[frameKbn(frameList[selectedFrame]), 'demo_' + frameList[selectedFrame]]" v-on:click="pushBtn('FRAME')"/>
      <span v-show="!isImg(frameList[selectedFrame], 0)" class="demo" :style="'background-color:' + frameList[selectedFrame]" v-on:click="pushBtn('FRAME')"/>
      <div class="sp_line_s" />
      <button class="btnBase" v-on:click="pushBtn('FRAME')">フレーム変更</button>
      <div v-show="isSelectFrame">
        <div class="sp_line_m" />
        <div style="display:flex; gap:5px; flex-wrap:wrap; width:90%; margin:auto; justify-content:space-evenly">
          <div v-for="(val, index) in frameList" :key="index">
            <span v-show="isImg(val, 0)" class="demo" :class="[selFrame(index), frameKbn(val), 'demo_' + val]" v-on:click="chFrame(index)" />
            <span v-show="!isImg(val, 0)" class="demo" :class="selFrame(index)" :style="'background-color:' + val" v-on:click="chFrame(index)" />

          </div>
        </div>
      </div>

      <div class="sp_line_l" />
      <div class="dotBar" />
      <div class="sp_line_m" />

      <div v-show="status == 'STOP'">
        <img :src="this.$root.IMG_PATH + 'icon_midashi3.png'" style="width:250px">
        <!-- <span class="stepNo font_xl">3</span><span class="font_b font_xl">時間の修正をしましょう</span> -->
        <div class="sp_line_m" />
        <input type="text" class="form-control inputBase" style="width:50px;display:inline; text-align:center" placeholder="00" maxlength="2"
        v-model="cHour">
        <span style="margin:0 10px">時間</span>
        <input type="text" class="form-control inputBase" style="width:50px;display:inline; text-align:center" placeholder="00" maxlength="2"
        v-model="cMinute">
        <span style="margin:0 10px">分</span>
        <input type="text" class="form-control inputBase" style="width:50px;display:inline; text-align:center" placeholder="00" maxlength="2"
        v-model="cSecond">
        <span style="margin-left:10px">秒</span>

        <div class="sp_line_l" />
        <div class="dotBar" />
        <div class="sp_line_m" />

      </div>

      <img v-show="status != 'STOP'" :src="this.$root.IMG_PATH + 'icon_midashi4S.png'" style="width:250px">
      <img v-show="status == 'STOP'" :src="this.$root.IMG_PATH + 'icon_midashi4.png'" style="width:250px">
      <!-- <span class="font_b font_xl">デザインを確認してください</span> -->
      <div class="sp_line_m" />
      <div :style="'width:95%; height:' + (314 * cardBaseScale) + 'px; margin:auto;'" class="cardFC">
        <div id="cardBase" :style="'border: 1px solid rgba(88,88,88,0.4); position:relative; width:600px; height:314px; margin:auto; font-family: \'Zen Maru Gothic\', \'Kosugi Maru\'; transform-origin: top left; transform : scale(' + cardBaseScale + ')'">
          <img v-show="isImg(frameList[selectedFrame], 0)" :src="this.$root.IMG_PATH + 'frame/card_base_' + frameList[selectedFrame] + '.png'" style="width:100%">

          <div style="width:100%; height:100%" :style="'background-color:' + frameList[selectedFrame] + '; display:' + isImg(frameList[selectedFrame], 1)" ></div>

          <img :src="this.$root.IMG_PATH + 'frame/card_base_front.png'" style="width:100%; position:absolute; top: 0; left: 0">
 
          <span id="cardTitle" style="position:absolute; top:30%; left:50%; font-size:10px; line-height: 37px; transform: translate(-50%, -50%) !important; word-break: break-all; -webkit-transform: translate(-50%, -50%) !important; -ms-transform: translate(-50%, -50%) !important;" :style="cardFont(0)">{{titleName}}</span>

          <span style="position:absolute; top:55%; left:50%; font-size:10px; white-space: nowrap; transform: translate(-50%, -50%) !important; -webkit-transform: translate(-50%, -50%) !important; -ms-transform: translate(-50%, -50%) !important;" :style="cardFont(1)">{{labelArray[1]}}</span>

          <span v-show="this.status == 'STOP'" style="position:absolute; bottom:12%; right:5%; white-space: nowrap; font-size:20px; transform: translateY(-50%) !important; -webkit-transform: translateY(-50%) !important; -ms-transform: translateY(-50%) !important;">{{cardTimeLabel}}</span>
        </div>
      </div>
      
      <div class="sp_line_l" />
      <div class="dotBar" />
      <div class="sp_line_m" />

      <img v-show="status != 'STOP'" :src="this.$root.IMG_PATH + 'icon_midashi5S.png'" style="width:250px">
      <img v-show="status == 'STOP'" :src="this.$root.IMG_PATH + 'icon_midashi5.png'" style="width:250px">

      <div class="sp_line_m" />
      <span class="font_l">「ツイートする」ボタンを押すとTwitterの投稿画面が開きます。<br>
      その他のSNSや画像として保存する場合は、「DOWNLOAD」ボタンを押してください。</span>

      <div class="sp_line_m" />

      <p v-show="errMsg != ''" class="errMsg">{{errMsg}}</p>
      <button v-show="status != 'STOP'" class="btnBase" v-on:click="pushBtn('TWEET_START')">ツイートする</button>
      <button v-show="status == 'STOP'" class="btnBase" v-on:click="pushBtn('TWEET_END')">ツイートする</button>
      <button class="btnBase" v-on:click="pushBtn('DOWNLOAD')">DOWNLOAD</button>
      
      <div class="sp_line_l" />
      <p class="font_m errMsg" style="text-align:left; width:90%; margin:auto">
        注意） <br>
        ・「ツイートする」ボタンを押して際に、投稿画面に表示されているURLは消さないでください。デザインした画像が表示されなくなります。本文やハッシュタグは編集してもOKです。<br>
        ・一部の端末ではTwitterが開かない場合があります。
      </p>
    </div>
  </div>
  <div class="sp_line_m" />

  <iframe src='https://d830x8j3o1b2k.cloudfront.net/adfurikun/api/get-script/locale/ja/app_id/6488673cab0be471ba6447c2/ssl/1/iframe/1' width='320' height='50' style='border:none;margin:0 auto;padding:0;display:block;'></iframe>

  <div class="sp_line_m" />
</div>
</template>

<script>
import axios from 'axios'
// import postscribe from 'postscribe'

export default {
  name: 'Top',
  components: {
  },
  data() {
    return {
      status : "INIT",
      
      startTime : null,
      timerValue : 0,
      currentTime : 0,

      titleName : "",
      cHour : "",
      cMinute : "",
      cSecond : "",

      frameList : [
        "#F8C5CB", "#E3F6E8", "#FBF4D9", "#CDE7FC", "#F6DEFF", "#FFC6FE", "#FDF2F3", "#D6ECF3", "#DBF0F5", "#EDB6DA", "#F7D0CD", "#F7F9CD", "#E3F5DB", "#B6BEED", "#FFE2EE", "#FFF3D0", "#F4E6FD", "#FBC6DA", "#D1EFF1", "#E1F2F4",

        
        "#C8AFAF", "#DBB0B0", "#A7AFAF", "#C0D6D6", "#95C8AB", "#A9CBB7", "#D6B69D", "#D6D29D", "#9DD69E", "#9DCFD6", "#9DAAD6", "#BD9FBC", "#D5DBC4", "#E8B696", "#E1B8B6", "#CDB1C1", "#83B3B6", "#C7CD85", "#B3DBA9", "#B4BF95",

        
        "#FCD8FD", "#D1F7E6", "#FFE0DA", "#A9F0FC", "#B7D0FF", "#E2EFFF", "#FAFFE2", "#FFF1FF", "#C9FFE3", "#FFFCD9", 

        "#7FFF91", "#FF433B", "#7A52FF", "#C76CFC", "#A112C5", "#2D23D2", "#FFEC67", "#FF279F", "#EC5289", "#5A2199", "#DC238A", "#F85F5F", "#7762DE", "#3DB2FA", "#49CADC", "#F6F7D8", "#FFAB6C", "#F300E8", "#FF6196", "#FFF040",


        "sc000","sc001","sc002","sc003","sc004","sc005","sc006","sc007","sc008","sc009","sc010","sc011","sc012","sc013","sc014","sc015","sc016","sc017"],
      selectedFrame : 0,
      isSelectFrame : false,

      labelArray : ["", "の勉強を始めました"],
      labelArray1 : ["img_make_start.png", "の勉強を始めました"],
      labelArray2 : ["img_make_stop.png", "の勉強を終了しました"],
 
      baseParentWidth : 1000,
      cardBaseScale : 1.0,

      isHowto : false,

      errMsg : "",

    }
  },
  created : function () {
  },
  computed : {
    isImg : function () {
      return (frameName, kbn) => {
        if (frameName == null) {
          return false;
        }
        if (frameName.indexOf("#") != -1) {
          if (kbn == 0) {
            return false;
          } else {
            return 'inline-block';
          }
        } else {
          if (kbn == 0) {
            return true;
          } else {
            return 'none';
          }
        }
      }
    },
    timerFontSize : function () {
      let fSize = this.baseParentWidth / 6.5;
      if (fSize > 90) {
        fSize = 90;
      }
      return "font-size:" + fSize + "px";
    },
    timerLabel : function () {
      var hours = Math.floor(this.timerValue / 3600);
      var minutes = Math.floor((this.timerValue % 3600) / 60);
      var remainingSeconds = this.timerValue % 60;

      var formattedTime = '';
      formattedTime += hours.toString().padStart(2, '0') + ':';
      formattedTime += minutes.toString().padStart(2, '0') + ':';
      formattedTime += remainingSeconds.toString().padStart(2, '0');
      return formattedTime;
    },
    cardTimeLabel : function () {
      var formattedTime = '';
      if (this.cHour != "" && parseInt(this.cHour) != 0) {
        if (isNaN(parseInt(this.cHour))) {
          formattedTime += '--時間';
        } else {
          formattedTime += parseInt(this.cHour) + '時間';
        }
      }
      if (this.cMinute != "" && parseInt(this.cMinute) != 0) {
        if (isNaN(parseInt(this.cMinute))) {
          formattedTime += '--分';
        } else {
          formattedTime += parseInt(this.cMinute) + '分';
        }
      }
      if (this.cSecond != "" && parseInt(this.cSecond) != 0) {
        if (isNaN(parseInt(this.cSecond))) {
          formattedTime += '--秒';
        } else {
          formattedTime += parseInt(this.cSecond) + '秒';
        }
      }
      
      if (parseInt(this.cHour) == 0 && parseInt(this.cMinute) == 0 && parseInt(this.cSecond) == 0) {
        formattedTime = "";
      }

      return formattedTime;
    },
    frameKbn : function () {
      return (frameName) => {
        if (frameName.indexOf("ba", 0) != -1) {
          return "demoBa";
        } else if (frameName.indexOf("sc", 0) != -1) {
          return "demoSc";
        }
      }
    },
    selFrame : function () {
      return (index) => {
        if (index == this.selectedFrame) {
          return "selectFrame";
        } else {
          return "";
        }
      }
    },
    cardFont : function () {
      return (kbn) => {
        let cardWidth = 600;
        if (kbn == 0) {
          //タイトル
          let tLength = 0;
          for (let i = 0; i < this.titleName.length; i++) {
            const charCode = this.titleName.charCodeAt(i);
            if ((charCode >= 0x0020 && charCode <= 0x007E) || (charCode >= 0xFF61 && charCode <= 0xFF9F)) {
              tLength += 1; // 半角文字
            } else {
              tLength += 2; // 全角文字
            }
          }

          let fSize = 40;
          if (fSize > (cardWidth * 0.75 / tLength) * 2) {
            fSize = (cardWidth * 0.75 / tLength) * 2;
            if (fSize < 25) {
              //2行にする
              return "font-size: 25px; white-space: normal; width: " + (cardWidth * 0.75) + "px";
            }
          }
          
          return "font-size:" + fSize + "px; white-space: nowrap;";
        } else if (kbn == 1) {
          //ラベル
          return "font-size:" + "20" + "px";
        }
      }
    },
  },
  mounted () {
    this.$nextTick(function() {
      //タイトル
      this.titleName = localStorage.getItem("TITLE_NAME") || "";
       
      // // eslint-disable-next-line
      // postscribe('#data-script', `<div><script type="text/javascript"> var _adf_global = {base: {}}; _adf_global.base.ad_id = "647e57ee38396d73e85f9099";_adf_global.base.iframe_width = 320; _adf_global.base.iframe_height = 50;  _adf_global.base.reload_span_sec = 30; _adf_global.base.disable_reload = false; <\/script><script type="text/javascript" src="//d1bqhgjuxdf1ml.cloudfront.net/js/adf_global_base_v1.2.min.js" charset="UTF-8"><\/script><\/div>
      // `, {
      //     done: function() {
      //         console.log("OK");
      //     }
      // }) 

    });

    this.baseParentWidth = document.getElementById('baseParent').clientWidth;
    this.cardBaseScale = this.baseParentWidth * 0.95 * 0.95 / 600.0;
    if (this.cardBaseScale > 1) {
      this.cardBaseScale = 1.0;
    }
    window.addEventListener('resize', this.calculateWindowWidth);
  },
  methods : {
    pushHowto : function () {
      this.isHowto = !this.isHowto;
    },
    calculateWindowWidth : function () {
      this.baseParentWidth = document.getElementById("baseParent").clientWidth;
      this.cardBaseScale = this.baseParentWidth * 0.95 * 0.95 / 600.0;
      if (this.cardBaseScale > 1) {
        this.cardBaseScale = 1.0;
      }
    },
    pushBtn : function (name) {
      if (name == "FRAME") {
        this.isSelectFrame = !this.isSelectFrame;
      } else if (name == "TWEET_START" || name == "TWEET_END") {
        this.$root.isMaking = true;
        this.errMsg = "";

        //入力チェック
        if (this.titleName == "") {
          this.errMsg = "タイトルを入力してください";
          this.$root.isMaking = false;
          return;
        } else if (this.titleName.length > 50) {
          this.errMsg = "タイトルは50文字以下で入力してください";
          this.$root.isMaking = false;
          return;
        }

        let kbn = 0;
        if (name == "TWEET_END") {
          if (this.cHour == "") {
            this.cHour = "0";
          }
          if (this.cMinute == "") {
            this.cMinute = "0";
          }
          if (this.cSecond == "") {
            this.cSecond = "0";
          }
          
          if (isNaN(parseInt(this.cHour)) || parseInt(this.cHour) < 0 || isNaN(parseInt(this.cMinute)) || parseInt(this.cMinute) < 0 || isNaN(parseInt(this.cSecond)) || parseInt(this.cSecond) < 0) {
            this.errMsg = "正しい時間を入力してください";
            this.$root.isMaking = false;
            return;
          }
          kbn = 1;
        }

        //現在の情報をDBに保存
        let params = new URLSearchParams();
        params.append('titleName', this.titleName);
        params.append('frameIndex', this.frameList[this.selectedFrame]);
        params.append('kbn', kbn);

        if (name == "TWEET_END") {
          let studyTime = parseInt(this.cHour) * 60 * 60 + parseInt(this.cMinute) * 60 + parseInt(this.cSecond);
          params.append("studyTime", studyTime);
        }

        axios.post(this.$root.SERVER_PATH + "reportStudy", params)
        .then((response) => {
          let resData = response.data;

          if (resData["status"] == this.$root.API_SUCCESS) {
            //成功
            let link = resData["code"];
            let text = this.titleName;

            if (name == "TWEET_START") {
              text = text + "の勉強をはじめました。";
            } else {
              let timeStr = "";
              if (parseInt(this.cHour) != 0) {
                timeStr = timeStr + this.cHour + "時間";
              }
              if (parseInt(this.cMinute) != 0) {
                timeStr = timeStr + this.cMinute + "分";
              }
              if (parseInt(this.cSecond) != 0) {
                timeStr = timeStr + this.cSecond + "秒";
              }
              text = text + "の勉強を終了しました。 "
              
              if (timeStr != "") {
                text = text + "勉強時間：" + timeStr;
              }
            }

            var url = "https://twitter.com/intent/tweet?" + 
            "hashtags=" + encodeURIComponent('まなんだー') + 
            "&text=" + encodeURIComponent(text) + 
            "&url=" + encodeURIComponent(this.$root.BASE_URL + link);
            window.open(url);
            this.$root.isMaking = false;
          } 
          // if (resData["status"] == this.$root.API_FAILED_MISMATCH) {
          //   this.$refs.popup.errorMessage = "暗証番号が正しくありません";
          // } else if (resData["status"] == this.$root.API_SUCCESS) {
          //   this.$refs.popup.title = "";
          //   this.$refs.popup.body = "";
          //   this.$refs.popup.showModal("DELETE_SUCCESS");
          // }
          //  else if (resData["status"] == this.$root.API_SUCCESS) {
          // }
        });
      } else if (name == "DOWNLOAD") {
        //画像化
        this.$root.isMaking = true;
        window.html2canvas(document.getElementById('cardBase'), { 
          scale:1
        }).then(canvas => {
          // var base64 = canvas.toDataURL('image/jpeg');
          // base64 = base64.replace(/^.*,/, '');

          const a = document.createElement("a");
          a.href = canvas.toDataURL("image/jpeg", 1);
          a.download = "study_timer.png";
          a.click();
          this.$root.isMaking = false;

          // axios.post(this.$root.SERVER_PATH + "updateChallenge", params);      
        });
      }
    },
    chFrame : function (index) {
      this.selectedFrame = index;
      this.isSelectFrame = false;
      localStorage.setItem("SEL_FRAME", this.frameList[this.selectedFrame]);
    },
    chTitle : function () {
      localStorage.setItem("TITLE_NAME", this.titleName);
    },
    pushStatus : function (val) {
      this.status = val;
      this.labelArray = this.labelArray1;

      if (val == "RESET") {
        //リセット
        this.timerValue = 0;
        this.currentTime = 0;
        this.startTime = "";
        this.status = "INIT";
        localStorage.removeItem("START_TIME");
        localStorage.removeItem("CURRENT_SEC");
      } else if (val == "START") {
        //開始
        this.calculateWindowWidth();
        this.startTime = new Date();
        this.currentTime = 0;
        this.timerValue = 0;
        localStorage.setItem("START_TIME", this.startTime.toLocaleString());
        localStorage.setItem("CURRENT_SEC", 0);
      } else if (val == "STOP") {
        //終了
        this.currentTime = this.timerValue;
        this.startTime = null;
        localStorage.setItem("CURRENT_SEC", this.currentTime);
        localStorage.removeItem("START_TIME");
        this.labelArray = this.labelArray2;

        this.cHour = Math.floor(this.currentTime / 3600);
        this.cMinute = Math.floor((this.currentTime % 3600) / 60);
        this.cSecond = this.currentTime % 60;
      } else if (val == "PAUSE") {
        //一時停止
        //これまでの秒数を保管
        let addVal = Math.floor((new Date() - this.startTime) / 1000);
        this.currentTime += addVal;
        this.startTime = null;
        localStorage.setItem("CURRENT_SEC", this.currentTime);
        localStorage.removeItem("START_TIME");
      } else if (val == "RESUME") {
        //再開
        this.status = "START";
        this.startTime = new Date();
        localStorage.setItem("START_TIME", this.startTime.toLocaleString());
      }
      localStorage.setItem("STATUS", this.status);
    },
    initAction : function () {
      //前回までの状態を取得
      this.status = localStorage.getItem("STATUS") || "INIT";
      if (this.status == "STOP") {
        this.labelArray = this.labelArray2;
      } else {
        this.labelArray = this.labelArray1;
      }

      let cTime = localStorage.getItem("CURRENT_SEC");
      if (cTime == null) {
        this.currentTime = 0;
      } else {
        this.currentTime = parseInt(cTime);
      }

      let startStr = localStorage.getItem("START_TIME");
      if (startStr != null) {
        this.startTime = new Date(startStr);
      } else {
        this.startTime = null;
      }

      if (this.status == "STOP" || this.status == "MAKE") {
        this.cHour = Math.floor(this.currentTime / 3600);
        this.cMinute = Math.floor((this.currentTime % 3600) / 60);
        this.cSecond = this.currentTime % 60;
      }
      
      if (this.startTime != null) {
        this.timerValue = Math.floor((new Date() - this.startTime) / 1000) + this.currentTime;
      } else {
        this.timerValue = this.currentTime;
      }
      
      //フレーム
      this.selectedFrame = 0;
      let curFrameName = localStorage.getItem("SEL_FRAME") || "";
      for (let i = 0; i < this.frameList.length; i++) {
        if (curFrameName == this.frameList[i]) {
          this.selectedFrame = i;
          break;
        }
      }

      const countUp = function(){
        if (this.status == "START") {
          let val = 0;
          if (this.startTime != null) {
            val = Math.floor((new Date() - this.startTime) / 1000);
          }
          this.timerValue = val + this.currentTime;
        }
      }.bind(this);
      setInterval(countUp, 1000);
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  beforeRouteUpdate(to, from, next) {
    next(vm => {
      vm.initAction();
    });
  },
  watch : {
  }


}
</script>

