      } else if (this.illustKbn == 1) {
        this.illustList = this.iFemaleList;
      } else if (this.illustKbn == 2) {
        this.illustList = this.iOtherList;
      }
      this.selIllust = 0;
    },
    pushIllust : function (index) {
      this.selIllust = index;
