      let scale = 2;
      if (name == "SHARE") {
        scale = 1;
      }

      //画像化
      window.html2canvas(document.getElementById("cardBase"), { 
        scale:scale
      }).then(canvas => {
        // var base64 = canvas.toDataURL('image/jpeg');
        // base64 = base64.replace(/^.*,/, '');

        const a = document.createElement("a");
        let data = canvas.toDataURL("image/jpeg", 1);

        if (name == "DOWNLOAD") {
          a.href = data;
          a.download = "test.jpg";
          a.click();
        } else {
          //サーバーに送信
          this.shareData = data;
          this.shareAction();
        }

        // axios.post(this.$root.SERVER_PATH + "updateChallenge", params);      
      });
    },
    shareAction : function () {
      let config = {
          headers: {'Content-Type': 'multipart/form-data'}
      };

      let params = new FormData();
      params.append('file', this.shareData);
      params.append('baseFile', this.shareData);
      axios.post(this.$root.SERVER_PATH + 'shareAction', params, config)
      .then((response) => {
        let resData = response.data;

        if (resData["status"] == this.$root.API_SUCCESS) {
          //成功
          let link = resData["code"];
          
          let text = "学生・受験生のための掲示板「まなんだー」";

          var url = "https://twitter.com/intent/tweet?" + 
          "hashtags=" + encodeURIComponent('まなんだー') + 
          "&text=" + encodeURIComponent(text) + 
          "&url=" + encodeURIComponent(this.$root.BASE_URL + link);
          window.open(url);
          this.$root.isMaking = false;
        } else {
          this.errorMessage = "データの作成に失敗しました。";
        }
      });
    },
