        if (query["page"] == 1) {
          this.$root.showLoad();
          this.getPostList();
        }
        query["page"] = 1;
        this.$router.push({query});
      } else if (name == "SHARE") {
        this.makeImage(name);
      } else if (name == "DOWNLOAD") {
        this.makeImage(name);
      }
    },
