<template>
<!-- 一覧部 -->
<div v-show="list == null || list.length == 0">
  <div class="sp_line_m" />
  <p class="font_l">投稿はまだありません</p>
  <div class="sp_line_m" />
</div>

<div v-show="list.length != 0" v-for="(data, index) in list" :key="index">
  <ListRow :kbn="kbn" :isLatest="isLatest" :data="data" :rowIndex="index"
  @pushResponse="pushResponse" 
  @pushRowBtn="pushRowBtn" 
  ></ListRow>
</div>
<Popup ref="popupList" popupKbn="LIST" @deleteAction="deleteAction" @reportAction="reportAction" @popupAction="popupAction"></Popup>
<span id="listEnd" />
</template>

<script>
import axios from 'axios'
import Popup from './Popup.vue';
import ListRow from './ListRow.vue';

export default {
  props: {
    kbn : String,
    list: Array,
    isLatest : Boolean,
  },
  components: {
    Popup,
    ListRow,
  },
  data() {
    return {
      targetId : -1,
    }
  },
  created : function () {
  },
  computed : {
  },
  methods : {
    pushResponse : function (id) {
      this.$root.listRowAction(id, this.list, this.kbn);
    },
    pushRowBtn : function (name, id) {
      if (name == "STAMP") {
        //スタンプ
        let params = new URLSearchParams();
        params.append('postKbn', this.kbn);
        params.append('userId', this.$root.userId);
        params.append('postId', id);
        params.append('stampId', 1);

        axios.post(this.$root.SERVER_PATH + "addStamp", params)
        .then((response) => {
          let resData = response.data;
          if (resData["status"] == this.$root.API_SUCCESS) {
            this.$root.chkUserId(resData);

            //呼び出し元の処理
            this.$emit("rowAction", {kbn : "STAMP", id : id});
          }
        });
      } else if (name == "RESPONSE") {
        //呼び出し元の処理
        this.$emit("rowAction", {kbn : name, id : id});
      } else if (name == "NO") {
        if (this.kbn == "REPORT" || this.kbn == "SOUDAN" || this.isLatest) {
          return;
        }
        
        this.$root.autoScroll("EDIT");
        
        //呼び出し元の処理
        this.$emit("rowAction", {kbn : name, id : id});
      } else if (name == "REPORT") {
        this.targetId = id;
        this.$refs.popupList.showModal("REPORT");
      } else if (name == "DELETE") {
        this.targetId = id;
        this.$refs.popupList.showModal("DELETE");
      }
    },
    deleteAction : function (val) {
      let params = new URLSearchParams();
      params.append('postKbn', this.kbn);
      params.append('postId', this.targetId);
      params.append('deleteNo', val);
      params.append('userId', this.$root.userId);

      axios.post(this.$root.SERVER_PATH + "deletePost", params)
      .then((response) => {
        let resData = response.data;

        if (resData["status"] == this.$root.API_FAILED_NODATA) {
          this.$refs.popupList.errorMessage = "その投稿は存在しません";
        } else if (resData["status"] == this.$root.API_FAILED_MISMATCH) {
          this.$refs.popupList.errorMessage = "暗証番号が正しくありません";
        } else if (resData["status"] == this.$root.API_SUCCESS) {
          this.$refs.popupList.title = "";
          this.$refs.popupList.body = "投稿を削除しました";
          this.$refs.popupList.showModal("DELETE_SUCCESS");
        }
      });
    },
    reportAction : function (val) {
      let params = new URLSearchParams();
      params.append('postKbn', this.kbn);
      params.append('postId', this.targetId);
      params.append('userId', this.$root.userId);
      params.append('message', val);
      axios.post(this.$root.SERVER_PATH + "reportPost", params)
      .then((response) => {
        let resData = response.data;

        this.$root.chkUserId(resData);

        if (resData["status"] == this.$root.API_FAILED_NODATA) {
          this.$refs.popupList.errorMessage = "その投稿は存在しません";
        } else if (resData["status"] == this.$root.API_SUCCESS) {
          this.$refs.popupList.title = "";
          this.$refs.popupList.body = "通報を受け付けました";
          this.$refs.popupList.showModal("REPORT_SUCCESS");
        }
      });
    },
    popupAction : function (kbn) {
      if (kbn == "DELETE_SUCCESS") {
          this.$emit("rowAction", {kbn : "DELETE", id : this.targetId});
          this.$refs.popupList.closeModal();
      }
    }
  },
  watch : {
  }


}
</script>

